body {
  background-color: rgb(50, 55, 56);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container {
  max-width: 1600px;
  margin: 0 auto;

}





@media (max-width: 1280px) {
  .container {
    max-width: 400px;
    margin: 0 auto;
    padding: 0 20px;
  }
}